/* src/styles.scss */

/* RESET-ish */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

/* THEME TOKENS */
:root {
  --bg: #f3f4f6;
  --bg-elevated: #ffffff;
  --header-bg: #111827;
  --border: rgba(15, 23, 42, 0.12);
  --muted-border: rgba(148, 163, 184, 0.5);
  --text: #0f172a;
  --muted: #6b7280;
  --accent: #2563eb;
  --accent-soft: rgba(37, 99, 235, 0.1);
  --danger: #ef4444;

  --card-radius: 14px;
  --card-shadow: 0 18px 45px rgba(15, 23, 42, 0.12);

  --tag-bg: rgba(148, 163, 184, 0.14);
  --text-on-accent: #f9fafb;

  --input-bg: #f9fafb;
}

/* DARK THEME OVERRIDES (html.dark injected by app.component.ts) */
html.dark {
  --bg: #020617;
  --bg-elevated: #020617;
  --header-bg: #020617;
  --border: rgba(148, 163, 184, 0.35);
  --muted-border: rgba(75, 85, 99, 0.9);
  --text: #e5e7eb;
  --muted: #9ca3af;
  --accent: #38bdf8;
  --accent-soft: rgba(56, 189, 248, 0.14);
  --danger: #f97373;

  --card-radius: 16px;
  --card-shadow: 0 20px 55px rgba(15, 23, 42, 0.85);

  --tag-bg: rgba(55, 65, 81, 0.8);
  --text-on-accent: #0b1120;

  --input-bg: #020617;
}

/* GLOBAL BODY STYLING */
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "SF Pro Text",
    "Segoe UI", sans-serif;
  background: radial-gradient(circle at top, #1d283a 0, var(--bg) 50%);
  color: var(--text);
}

/* UTILITY CLASSES */
.page-title {
  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: 0.02em;
  margin: 0 0 12px;
}

.page-subtitle {
  margin: 0 0 20px;
  color: var(--muted);
  font-size: 0.95rem;
}

/* LAYOUT CONTAINERS */
.list-page,
.detail-page,
.add-page {
  max-width: 1240px;
  margin: 0 auto;
  padding: 16px 12px 40px;
}

.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  margin-bottom: 16px;
}

/* BUTTONS */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 7px 13px;
  border-radius: 999px;
  border: 1px solid var(--muted-border);
  background: rgba(15, 23, 42, 0.02);
  color: var(--text);
  font-size: 0.88rem;
  cursor: pointer;
  text-decoration: none;
  transition: transform 120ms ease, box-shadow 120ms ease,
    background-color 120ms ease, border-color 120ms ease, opacity 120ms ease;
}

.btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 12px 30px rgba(15, 23, 42, 0.18);
  border-color: var(--accent);
}

.btn.primary {
  background: linear-gradient(135deg, var(--accent), #4f46e5);
  border-color: transparent;
  color: var(--text-on-accent);
  font-weight: 600;
}

.btn.danger {
  border-color: rgba(248, 113, 113, 0.6);
  color: var(--danger);
}

/* CHIPS / TAGS */
.chip {
  display: inline-flex;
  align-items: center;
  padding: 3px 9px;
  border-radius: 999px;
  background: var(--tag-bg);
  font-size: 0.78rem;
  color: var(--muted);
  gap: 4px;
}

/* INPUTS */
input[type="search"],
input[type="text"],
textarea {
  font-family: inherit;
  font-size: 0.92rem;
  border-radius: 999px;
  border: 1px solid var(--muted-border);
  background: var(--input-bg);
  color: var(--text);
  padding: 8px 12px;
  outline: none;
  transition: border-color 120ms ease, box-shadow 120ms ease,
    background-color 120ms ease;
}

textarea {
  border-radius: 12px;
  min-height: 80px;
}

input:focus,
textarea:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 1px var(--accent-soft);
}

/* CARDS */
.card {
  background: radial-gradient(circle at top left, rgba(148, 163, 184, 0.2) 0, var(--bg-elevated) 35%);
  border-radius: var(--card-radius);
  border: 1px solid var(--border);
  box-shadow: var(--card-shadow);
  padding: 16px 18px;
}

/* SCROLLBARS (optional) */
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-thumb {
  background: rgba(148, 163, 184, 0.5);
  border-radius: 999px;
}
html.dark ::-webkit-scrollbar-thumb {
  background: rgba(55, 65, 81, 0.9);
}
